import {
  ConditionMapper,
  MessageMapper,
} from './workflow-definition.interface';

export enum NodeTypes {
  TRIGGER = 'TRIGGER',
  ACTION = 'ACTION',
  CONDITIONAL = 'CONDITIONAL',
  VALIDATION = 'VALIDATION',
  ANNOTATION = 'ANNOTATION',
  PLACEHOLDER = 'PLACEHOLDER',
  INVISIBLE = 'INVISIBLE',
  END = 'END',
}

export enum ConditionalTypes {
  TRUE_FALSE = 'TRUE_FALSE',
  CASE = 'CASE',
}

export interface Conditional {
  type: ConditionalTypes;
}

export interface Node {
  id: string;
  type: NodeTypes;
  position: { x: number; y: number };
  meta?: Record<string, string | Array<string>>;
  name: string;
}

export interface ActionNode extends Node {
  type: NodeTypes.ACTION;
  action: {
    identifier: string;
    version: string;
  };
  with: MessageMapper;
}

export interface TriggerNode extends Node {
  type: NodeTypes.TRIGGER;
  trigger: {
    name: string;
    type: string;
    identifier: string;
  };
}

export interface ConditionalNode extends Node {
  type: NodeTypes.CONDITIONAL;
  condition: Conditional;
  with: ConditionMapper;
}

export interface ValidationNode extends Node {
  type: NodeTypes.VALIDATION;
  with: ConditionMapper;
}

export interface AnnotationNode extends Node {
  type: NodeTypes.ANNOTATION;
}

export interface EndNode extends Node {
  type: NodeTypes.END;
}

export interface PlaceholderNode extends Node {
  type: NodeTypes.PLACEHOLDER;
}

export interface InvisibleNode extends Node {
  type: NodeTypes.INVISIBLE;
}

export type WorkflowNode =
  | ActionNode
  | ConditionalNode
  | AnnotationNode
  | PlaceholderNode
  | ValidationNode
  | EndNode
  | InvisibleNode;

export interface Edge {
  id: string;
  source: {
    nodeId: string;
  };
  target: {
    nodeId: string;
  };
  type: string;
  meta?: Record<string, string | Array<string>>;
}

export interface CreateWorkflowDrawingInterface {
  name: string;
  description: string;
  identifier: string;
  ttl: number;
  trigger: TriggerNode;
  nodes: Array<WorkflowNode>;
  edges: Array<Edge>;
  enabled: boolean;
}

export interface WorkflowDrawingInterface
  extends CreateWorkflowDrawingInterface {
  id: string;
  workflowDefinitionId: string;
  createdAt: Date;
  updatedAt: Date;
  version: number;
}
